.page {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-color: hsl(217, 54%, 11%);
  font-family: 'Outfit', sans-serif;
}
.page__wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  max-width: 1440px;
  min-height: 100%;
  display: grid;
  grid-template-columns: minmax(0, 1fr) 375px minmax(0, 1fr);
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 400px) {
  .page__wrapper {
    grid-template-columns: minmax(0, 1fr) 320px minmax(0, 1fr);
  }
}
.card {
  box-sizing: border-box;
  margin: 0;
  padding: 25px;
  grid-column: 2;
  background-color: hsl(216, 50%, 16%);
  border-radius: 15px;
}
.card-banner {
  margin: 0;
  padding: 0;
  position: relative;
}

.card-banner:hover {
  cursor: pointer;
}

.card-banner:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  background-color: hsl(178, 100%, 50%);
  border-radius: 10px;
  opacity: .6;
}

.card-banner:hover::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  background-image: url(/nft-preview-card/static/media/icon-view.fedac551.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.card-banner__img {
  margin: 0;
  padding: 0;
  width: 100%;
  border-radius: 10px;
}
.card-title {
  margin: 0;
  padding: 25px 0;
  font-size: 25px;
  font-weight: 400;
  color: hsl(0, 0%, 100%);
  transition: all ease-in-out .2s;
}

.card-title:hover {
  cursor: pointer;
  color: hsl(178, 100%, 50%);
}
.card-description {
  margin: 0;
  padding: 0 0 25px;
  font-size: 20px;
  font-weight: 300;
  line-height: 1.5;
  color: hsl(215, 51%, 70%);
}
.price {
  margin: 0;
  padding: 0;
  display: flex;
}
.price__icon {
  margin: 0;
  padding: 0;
}
.price__value {
  margin: 0;
  padding: 0 0 0 7px;
  font-size: 18px;
  font-weight: 600;
  color: hsl(178, 100%, 50%);
}
.remaining-time {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: flex-start;
}
.remaining-time__icon {
  margin: 0;
  padding: 0;
  width: 20px;
  height: 20px;
}
.remaining-time__value {
  margin: 0;
  padding: 0 0 0 7px;
  font-size: 18px;
  font-weight: 300;
  color: hsl(215, 51%, 70%);
}
.product-info {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}
.divider {
  margin: 25px 0;
  padding: 0;
  height: 1px;
  width: 100%;
  background-color: hsl(215, 32%, 27%)
}
.creator-details {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}
.creator-avatar {
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 1px solid #fff;
}
.creator-info {
  margin: 0;
  padding: 0 0 0 20px;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
  color: hsl(215, 51%, 70%);
}
.creator-info__name {
  margin: 0;
  padding: 0;
  color: #fff;
  transition: all ease-in-out .2s;
}

.creator-info__name:hover {
  cursor: pointer;
  color: hsl(178, 100%, 50%);
}
